.dashboard {
  min-height: 100vh;
  background: #f4f6f9;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Header */
.header {
  background: #fff;
  padding: 1rem 2rem;
  box-shadow: 0 2px 4px rgba(0,0,0,.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header h1 {
  margin: 0;
  font-size: 1.8rem;
  color: #333;
}
.nav a {
  margin-left: 1.5rem;
  text-decoration: none;
  color: #007bff;
  font-weight: 500;
}
.nav a:hover { text-decoration: underline; }

/* Grid */
.grid {
  display: grid;
  gap: 1.5rem;
  padding: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
.card, .cardFull {
  background: #fff;
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: 0 2px 6px rgba(0,0,0,.08);
}
.cardFull { grid-column: 1 / -1; }
.card h3 {
  margin-top: 0;
  margin-bottom: 1rem;
  color: #444;
}

/* Map */
.map {
  height: 400px;
  border-radius: 6px;
}

/* Loading / Error */
.loading, .error {
  text-align: center;
  padding: 3rem;
  font-size: 1.2rem;
}
.error { color: #dc3545; }

/* Responsive tweaks */
@media (max-width: 768px) {
  .header { flex-direction: column; align-items: flex-start; }
  .nav { margin-top: 1rem; }
  .nav a { margin-left: 0; margin-right: 1rem; }
}
.pageContent {
  padding-top: 20px;
  min-height: calc(100vh - 64px);
  background: #f4f6f9;
}
.appWrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.pageContent {
  flex: 1;
  padding-top: 20px;
  background: #f4f6f9;
}