/* Edit Mode */
.editMode {
  margin-top: 0.5rem;
}
.editTextarea {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid #007bff;
  border-radius: 6px;
  font-size: 1rem;
  resize: vertical;
}
.editTextarea:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(0,123,255,.2);
}

.editActions {
  margin-top: 0.5rem;
  display: flex;
  gap: 0.5rem;
}
.saveBtn {
  background: #28a745;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
}
.saveBtn:disabled {
  background: #ccc;
  cursor: not-allowed;
}
.cancelBtn {
  background: #6c757d;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
}
.saveBtn:hover:not(:disabled), .cancelBtn:hover {
  opacity: 0.9;
}

.editBtn {
  background: transparent;
  color: #007bff;
  border: none;
  font-size: 0.9rem;
  cursor: pointer;
  text-decoration: underline;
  margin-top: 0.5rem;
}
.editBtn:hover {
  color: #0056b3;
}
.pageContent {
  padding-top: 20px;
  min-height: calc(100vh - 64px);
  background: #f4f6f9;
}
.appWrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.pageContent {
  flex: 1;
  padding-top: 20px;
  background: #f4f6f9;
}