/* Search Bar */
.searchBar {
  padding: 1rem 2rem;
  background: #fff;
  border-bottom: 1px solid #eee;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.searchInput {
  flex: 1;
  padding: 0.6rem 1rem;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
}
.searchInput:focus {
  outline: none;
  border-color: #007bff;
}
.clearBtn {
  background: #f1f1f1;
  border: none;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  font-size: 1.2rem;
  cursor: pointer;
}
.clearBtn:hover { background: #e0e0e0; }

/* Results Info */
.resultsInfo {
  padding: 0 2rem;
  font-size: 0.95rem;
  color: #666;
}

/* No Data */
.noData {
  text-align: center;
  color: #888;
  font-style: italic;
  padding: 2rem !important;
}

/* Pagination */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem 2rem;
  background: #fff;
  border-top: 1px solid #eee;
}
.pageBtn {
  padding: 0.5rem 1rem;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
}
.pageBtn:disabled {
  background: #ccc;
  cursor: not-allowed;
}
.pageBtn:hover:not(:disabled) {
  background: #0056b3;
}
.pageInfo {
  font-weight: 500;
  color: #444;
}
.pageContent {
  padding-top: 20px;
  min-height: calc(100vh - 64px);
  background: #f4f6f9;
}
.appWrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.pageContent {
  flex: 1;
  padding-top: 20px;
  background: #f4f6f9;
}