.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f0f4f8;
  font-family: Arial, sans-serif;
}

.title {
  font-size: 2rem;
  margin-bottom: 2rem;
  color: #333;
}

.form {
  display: flex;
  flex-direction: column;
  width: 300px;
}

.input {
  padding: 0.8rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

.button {
  padding: 0.8rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
}

.button:hover {
  background-color: #0056b3;
}

.toggle {
  margin-top: 1rem;
  color: #007bff;
  cursor: pointer;
}

.error {
  color: red;
  margin-top: 1rem;
}

/* Responsive for scalability (mobile integration) */
@media (max-width: 600px) {
  .form {
    width: 90%;
  }
}
.pageContent {
  padding-top: 20px;
  min-height: calc(100vh - 64px);
  background: #f4f6f9;
}